$theme: #2A65FF;

#arcontactus-widget {
    opacity   : 0;
    transition: 0.2s opacity;


    &.active {
        opacity: 1;
    }

    &.arcontactus-message {
        z-index : 10000;
        right   : 30px;
        bottom  : 20px;
        position: fixed !important;
        height  : 65px;
        width   : 65px;
    }

    .arcontactus-message-button {
        width             : 65px;
        position          : absolute;
        height            : 65px;
        background-color  : $theme;
        border-radius     : 50px;
        -webkit-box-sizing: border-box;
        box-sizing        : border-box;
        text-align        : center;
        display           : -webkit-box;
        display           : -ms-flexbox;
        display           : flex;
        -webkit-box-pack  : center;
        -ms-flex-pack     : center;
        justify-content   : center;
        -webkit-box-align : center;
        -ms-flex-align    : center;
        align-items       : center;
        cursor            : pointer;

        p {
            font-family: Ubuntu, Arial, sans-serif;
            color      : #fff;
            font-weight: 700;
            font-size  : 10px;
            line-height: 11px;
            margin     : 0
        }

        .pulsation {
            width            : 84px;
            height           : 84px;
            background-color : $theme;
            border-radius    : 50px;
            position         : absolute;
         
            z-index          : -1;
            -webkit-transform: scale(0);
            -ms-transform    : scale(0);
            transform        : scale(0);
            -webkit-animation: arcontactus-pulse 2s infinite;
            animation        : arcontactus-pulse 2s infinite
        }

    

        .static {
            position         : absolute;
            -webkit-animation: arcontactus-show-stat 6s infinite;
            animation        : arcontactus-show-stat 6s infinite;
            top              : 50%;
            left             : 50%;
            transform        : translate(-48%, -50%);
            width            : 52px;
            height           : 52px;
            display          : flex;
            align-items      : center;
            justify-content  : center;
            text-align       : center;

            img {
                display: inline
            }
        }
    }

    .pulsation:nth-of-type(2n) {
        -webkit-animation-delay: 5s;
        animation-delay        : .5s
    }

    

    .icons-line {
        top     : 10px;
        left    : 12px;
        display : -webkit-box;
        display : -ms-flexbox;
        display : flex;
        position: absolute;
        height  : 24px;

        &.stop {
            // -webkit-animation-play-state:paused;
            // animation-play-state        :paused
        }

        span {
            display: inline-block;
            width  : 24px;
            height : 24px;
            color  : $theme;

            svg {
                width : 24px;
                height: 24px;
            }
        }

        img,
        span {
            margin-right: 40px
        }
    }

    .static {
        &.hide {
            display: none
        }
    }

    .icons {
        &.hide {
            display: none
        }

        .icon:first-of-type {
            margin-left: 0
        }
    }

    .arcontactus-close {
        img {
            -webkit-transform : rotate(180deg) scale(0);
            -ms-transform     : rotate(180deg) scale(0);
            transform         : rotate(180deg) scale(0);
            -webkit-transition: ease-in .12s all;
            -o-transition     : ease-in .12s all;
            transition        : ease-in .12s all;
            display           : block;
        }

        &.show-messageners-block {
            img {
                -webkit-transform: rotate(0) scale(1);
                -ms-transform    : rotate(0) scale(1);
                transform        : rotate(0) scale(1);
            }
        }
    }

    .messangers-block {

        width                   : 355px;
        padding-top             : 50px;
        background-repeat       : no-repeat;
        background-position     : center;
        position                : absolute;
        bottom                  : 80px;
        right                   : 0px;
        display                 : flex;
        flex-direction          : column;
        align-items             : flex-start;
        padding                 : 14px 0;
        box-sizing              : border-box;
        border-radius           : 7px;
        transform: translateY(30px);
        opacity: 0;
        pointer-events: none;
        transition: transform 160ms ease-in-out, opacity 160ms ease-in-out;
        z-index                 : 10000;

        &:hover {
            .close_widget {
                visibility: visible;
                opacity   : 0.5;
            }
        }

        &.show-messageners-block {
            pointer-events: all;
            transform: translateY(0px);
            opacity: 1;
        }
    }

    .messanger {
        display              : -webkit-box;
        display              : -ms-flexbox;
        display              : flex;
        -webkit-box-orient   : horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction   : row;
        flex-direction       : row;
        -webkit-box-align    : center;
        -ms-flex-align       : center;
        align-items          : center;
        margin               : 0;
        cursor               : pointer;
        width                : 100%;
        padding              : 8px 20px 8px 60px;
        position             : relative;
        min-height           : 54px;
        text-decoration      : none;

        &:hover {
            background-color: #EEEEEE;
        }

        span {
            position        : absolute;
            left            : 10px;
            top             : 50%;
            margin-top      : -20px;
            display         : block;
            width           : 40px;
            height          : 40px;
            border-radius   : 50px;
            background-color: #0084ff;
            margin-right    : 10px;
            color           : #FFFFFF;
            text-align      : center;
            vertical-align  : middle;

            svg {
                width         : 24px;
                height        : 24px;
                vertical-align: middle;
                text-align    : center;
                display       : block;
                position      : absolute;
                top           : 50%;
                left          : 50%;
                margin-top    : -12px;
                margin-left   : -12px;
            }
        }

        p {
            margin     : 0;
            font-family: Arial, sans-serif;
            font-size  : 14px;
            color      : rgba(0, 0, 0, .87)
        }
    }

    .callback-countdown-block {
        background              : #FFFFFF;
        box-shadow              : 0 0 10px rgba(0, 0, 0, 0.6);
        width                   : 410px;
        height                  : 150px;
        background-repeat       : no-repeat;
        background-position     : center;
        position                : absolute;
        bottom                  : 80px;
        left                    : auto;
        right                   : 0;
        -webkit-box-orient      : vertical;
        -webkit-box-direction   : normal;
        -ms-flex-direction      : column;
        flex-direction          : column;
        -webkit-box-align       : center;
        -ms-flex-align          : center;
        align-items             : center;
        -webkit-box-sizing      : border-box;
        box-sizing              : border-box;
        border-radius           : 7px;
        -webkit-transform-origin: 80% 105%;
        -ms-transform-origin    : 80% 105%;
        transform-origin        : 80% 105%;
        -webkit-transform       : scale(1);
        -ms-transform           : scale(1);
        transform               : scale(1);
        -webkit-transition      : ease-out .12s all;
        -o-transition           : ease-out .12s all;
        transition              : ease-out .12s all;
        z-index                 : 10000;
        color                   : $theme;
        padding-top             : 5px;
        padding-left            : 8px;
        padding-right           : 19px;
        display                 : none;

        &.display-flex {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex
        }

        &:before {
            position    : absolute;
            bottom      : -7px;
            right       : 25px;
            left        : auto;
            display     : inline-block !important;
            border-right: 8px solid transparent;
            border-top  : 8px solid #FFFFFF;
            border-left : 8px solid transparent;
            content     : '';
        }

        .callback-countdown-block-phone {
            font-family          : Arial, sans-serif;
            font-size            : 14px;
            line-height          : 16px;
            padding-left         : 10px;
            padding-right        : 10px;
            -webkit-box-orient   : vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction   : column;
            flex-direction       : column;
            -webkit-box-align    : center;
            -ms-flex-align       : center;
            align-items          : center;
            -webkit-box-pack     : center;
            -ms-flex-pack        : center;
            justify-content      : center;
            height               : 140px;
            display              : none;
            position             : relative;

            &:before {
                transition: 0.2s all;
            }

            &.ar-loading {
                &:before {
                    background: rgba(255, 255, 255, 0.8);
                    content   : ' ';
                    position  : absolute;
                    width     : 100%;
                    height    : 100%;
                    left      : 0;
                    top       : 0;
                    z-index   : 1;
                }

                &:after {
                    content      : ' ';
                    position     : absolute;
                    width        : 30px;
                    height       : 30px;
                    z-index      : 2;
                    top          : 50%;
                    bottom       : 50%;
                    margin-top   : -15px;
                    margin-bottom: -15px;
                    background   : url('../img/ring-alt.gif') no-repeat transparent scroll 0 0;
                }
            }

            p {
                text-align   : center;
                margin-bottom: 10px;
                margin-top   : 3px
            }

            .callback-countdown-block-form-group {
                display          : -webkit-box;
                display          : -ms-flexbox;
                display          : flex;
                -webkit-box-align: center;
                -ms-flex-align   : center;
                align-items      : center;

                input[type=tel] {
                    font-family       : Arial, sans-serif;
                    font-size         : 14px;
                    line-height       : 16px;
                    border-radius     : 4px;
                    border            : 0;
                    width             : 203px;
                    height            : 36px;
                    -webkit-box-sizing: border-box;
                    box-sizing        : border-box;
                    padding           : 10px 11px 9px;
                    border            : 1px solid #DDDDDD;
                }

                input[type=submit] {
                    border-radius   : 4px;
                    border          : 0;
                    background-color: $theme;
                    color           : #fff;
                    font-family     : Arial, sans-serif;
                    font-size       : 14px;
                    padding         : 10px 5px 9px;
                    cursor          : pointer;
                    width           : 132px;
                    height          : 36px;
                    margin-left     : 5px;

                    &:hover {
                        opacity: 0.8;
                    }
                }
            }
        }

        .callback-countdown-block-sorry {
            height           : 140px;
            -webkit-box-align: center;
            -ms-flex-align   : center;
            align-items      : center;
            display          : none;

            p {
                font-family  : Arial, sans-serif;
                font-size    : 16px;
                line-height  : 18px;
                text-align   : center;
                margin-bottom: 5px;
                margin-top   : 7px
            }
        }

        .callback-countdown-block-close {
            position: absolute;
            top     : 9px;
            right   : 9px;
            cursor  : pointer;
            z-index : 1;
        }

        .callback-countdown-block-timer {
            -webkit-box-orient   : vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction   : column;
            flex-direction       : column;
            -webkit-box-pack     : center;
            -ms-flex-pack        : center;
            justify-content      : center;
            -webkit-box-align    : center;
            -ms-flex-align       : center;
            align-items          : center;
            height               : 140px;
            display              : none;

            p {
                font-family  : Arial, sans-serif;
                font-size    : 14px;
                line-height  : 16px;
                text-align   : center;
                margin-bottom: 5px;
                margin-top   : 7px
            }

            h1 {
                font-family: Ubuntu, Arial, sans-serif;
                font-size  : 40px;
                line-height: 46px;
                text-align : center;
                font-weight: 300;
            }
        }

        .callback-countdown-block-phone.display-flex,
        .callback-countdown-block-timer.display-flex,
        .callback-countdown-block-sorry.display-flex {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex
        }
    }

    .callback-state {
        background-color : #fff;
        width            : 44px;
        height           : 44px;
        border-radius    : 50px;
        position         : absolute;
        overflow         : hidden;
        -webkit-animation: arcontactus-show-icons 6s infinite;
        animation        : arcontactus-show-icons 6s infinite;
        top              : 50%;
        left             : 50%;
        margin-top       : -22px;
        margin-left      : -22px;
        display          : none;
        -webkit-animation: none;
        animation        : none;
        z-index          : 999999;
        color            : $theme;

        &.display-flex {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex
        }

        svg {
            width      : 24px;
            height     : 24px;
            position   : absolute;
            top        : 50%;
            left       : 50%;
            margin-top : -12px;
            margin-left: -12px;
        }

        .callback-state-img {
            position: absolute;
            top     : 12px;
            left    : 12px
        }
    }

}


.widget__dialog {
    background   : #FFFFFF;
    border-radius: 8px;
    padding      : 20px 25px;

    p {
        font-size     : 14px;
        line-height   : 22px;
        letter-spacing: -0.35px;
        color         : #2F2E39;
        text-align    : left;
        margin-bottom : 0px;
        margin-right  : 35px;
    }
}

.widget__breadcrumbs {
    display: flex;

    ul {
        list-style     : none;
        margin         : 0;
        padding        : 0;
        display        : flex;
        justify-content: center;
        align-items    : center;

        li {
            font-size: 11px;
            color    : #2F2E39;
        }
    }
}

.consultant__status {
    opacity    : 0.5;
    margin-left: 15px;
    position   : relative;

    &:before {
        position      : absolute;
        bottom        : 4px;
        right         : auto;
        left          : -10px;
        display       : inline-block !important;
        content       : '';
        background    : #2F2E39;
        mix-blend-mode: normal;
        opacity       : 0.3;
        border-radius : 50%;
        width         : 4px;
        height        : 4px;
    }
}

.widget__box {
    width          : 100%;
    display        : flex;
    justify-content: flex-end;
    align-items    : flex-end;
}

.justiva__widge__avatar {
    width          : 40px;
    height         : 40px;
    border-radius  : 50%;
    overflow       : hidden;
    margin-right   : 15px;
    display        : flex;
    justify-content: flex-end;

    img {
        width: 100%;
    }
}

.widget__links {
    display        : flex;
    justify-content: flex-end;
    flex-wrap      : wrap;

    a {
        background     : #E7F6FF;
        border         : 1px solid rgba(42, 101, 255, 0.4);
        border-radius  : 8px;
        text-decoration: none;
        font-size      : 14px;
        line-height    : 22px;
        letter-spacing : -0.35px;
        color          : #2A65FF;
        padding        : 10px 15px;
        margin         : 5px 0px 0px 10px;
    }
}

.close_widget {
    position       : absolute;
    top            : 5px;
    right          : 0;
    width          : 31px;
    height         : 31px;
    background     : rgba(0, 0, 0, 0.3);
    border-radius  : 50%;
    display        : flex;
    justify-content: center;
    align-items    : center;
    cursor         : pointer;
    opacity        : 0;
    visibility     : hidden;
    transform      : translateZ(0);
    will-change    : auto;
    transition     : opacity .25s ease-in-out, visibility .25s;

    svg {
        display: block;
    }
}

#arcontactus-widget {
    .messangers-block {
        padding-top: 50px;
    }
}

.widget_container.show_widget:before {
       content: " ";
    position: fixed;
    right: -5px;
    bottom: -5px;
    z-index: 10;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    box-shadow: 0px -25px 300px 340px rgba(26, 58, 140, 0.24)
}

svg.widget_icon {
    fill: #fff;
}

#arcontactus-widget .arcontactus-message-button:hover .widget_icon {
    // transform : scale(1.03);
    // transition: 0.5s ease;

}

.widget__links a:hover {
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    background: #2A65FF;
    color     : #fff;
}
.show_widget {
    #arcontactus-widget {
        .arcontactus-message-button  .pulsation {
             -webkit-animation: none;
             animation        : none;
         }
     }
}


.messangers-block.show-messageners-block .widget__links a:nth-of-type(1) {
    transition: transform 0.35s ease-in-out, opacity 0.35s ease-in-out;
    transform: translateY(0px);
    opacity:1;
}

.messangers-block.show-messageners-block .widget__links a:nth-of-type(2) {
    transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
    transform: translateY(0px);
    opacity:1;
}


.messangers-block.show-messageners-block .widget__links a:nth-of-type(3) {
    transition: transform 0.45s ease-in-out, opacity 0.45s ease-in-out;
    transform: translateY(0px);
    opacity:1;
}
.messangers-block .widget__links a {
    transform: translateY(20px);
    opacity:0;
}

@-webkit-keyframes arcontactus-pulse{
    0%{
        -webkit-transform:scale(0);
        transform:scale(0);
        opacity:1
    }
    50%{
        opacity:.5
    }
    100%{
        -webkit-transform:scale(1);
        transform:scale(1);
        opacity:0
    }
}

@media (max-width: 368px) {
    #arcontactus-widget .messangers-block {
        width: 295px;
    }

    #arcontactus-widget.arcontactus-message {
        right: 10px;
    }

    .widget__dialog p {
        margin-right: 0;
    }

    .widget__breadcrumbs ul li {
        font-size: 10px;
    }
}
